
import CommonHeader from '@/pages/common/header.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
name: 'Personal',
data () {
return {
index: 0
}
},
computed: {
userInfo () {
return JSON.parse(localStorage.getItem('userInfo'))
}
},
components: {
CommonHeader,
Tab,
TabItem
},
activated () {
this.index = 0
}
}



